@keyframes ajaxify-lazy-rotate {
	100% {
		transform: rotate(360deg);
	}
}
#wpac-lazy-load-content {
	transition: opacity 0.5s ease-in-out;
	display: none;
	opacity: 0;
}
.ajaxify-comments-spinner__wrapper {
	position: relative;
	background-color: var(--ajaxify-comments-spinner-container-background-color, #333 );
	padding: var( --ajaxify-comments-spinner-container-padding, 30px );

	.ajaxify-admin__preview-controls {
		position: absolute;
		top: 10px;
		right: 10px;
		background: #FFF;
	}

	.ajaxify-comments-spinner__label {
		color: var( --ajaxify-comments-spinner-label-color, #FFFFFF );
	}
}

/**
 * Layout
 */
.ajaxify-comments-spinner__wrapper {
	&.ajaxify-comments-spinner__layout--vertical .ajaxify-comments-spinner__inner {
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	&.ajaxify-comments-spinner__layout--horizontal .ajaxify-comments-spinner__inner {
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
	}
	&.ajaxify-comments-spinner__alignment--left.ajaxify-comments-spinner__layout--horizontal .ajaxify-comments-spinner__inner {
		justify-content: flex-start;
	}
	&.ajaxify-comments-spinner__alignment--center.ajaxify-comments-spinner__layout--horizontal .ajaxify-comments-spinner__inner {
		justify-content: center;
	}
	&.ajaxify-comments-spinner__alignment--right.ajaxify-comments-spinner__layout--horizontal .ajaxify-comments-spinner__inner {
		justify-content: flex-end;
	}
	&.ajaxify-comments-spinner__alignment--left.ajaxify-comments-spinner__layout--vertical .ajaxify-comments-spinner__inner {
		align-items: flex-start;
	}
	&.ajaxify-comments-spinner__alignment--center.ajaxify-comments-spinner__layout--vertical .ajaxify-comments-spinner__inner {
		align-items: center;
	}
	&.ajaxify-comments-spinner__alignment--right.ajaxify-comments-spinner__layout--vertical .ajaxify-comments-spinner__inner {
		align-items: flex-end;
	}
	&.ajaxify-comments-spinner__rtl.ajaxify-comments-spinner__layout--horizontal .ajaxify-comments-spinner__inner {
		flex-direction: row-reverse;
	}
	&.ajaxify-comments-spinner__rtl.ajaxify-comments-spinner__layout--vertical .ajaxify-comments-spinner__inner {
		flex-direction: column-reverse;
	}
}
.ajaxify-comments-spinner__wrapper.ajaxify-layout-left {
	
}
.ajaxify-comments-spinner__inner {
	display: flex;
	align-items: center;
	font-size: var( --ajaxify-comments-spinner-container-font-size, 32px );
	line-height: var( --ajaxify-comments-spinner-container-line-height, 48px );
	color: var( --ajaxify-comments-spinner-container-label-color, #FFFFFF );
	row-gap: var( --ajaxify-comments-spinner-icon-gap, 20px );
	column-gap: var( --ajaxify-comments-spinner-icon-gap, 20px );
}
.ajaxify-comments-spinner__icon {
	
}
.ajaxify-comments-spinner__icon svg {
	display: block;
	animation: ajaxify-lazy-rotate var( --ajaxify-comments-spinner-icon-animation-speed, 1.25s ) linear infinite;
	color: var( --ajaxify-comments-spinner-icon-color, #FFFFFF );
	fill: var( --ajaxify-comments-spinner-icon-color, #FFFFFF );
	width: var( --ajaxify-comments-spinner-icon-size, 72px );
	height: var( --ajaxify-comments-spinner-icon-size, 72px );
}